    <uni-popup ref="nicknamePopup" type="center" :is-mask-click="false">
      <view class="edit-popup-container">
        <view class="popup-header-with-save">
          <text class="popup-action-btn" @click="closeNicknamePopup">ÂèñÊ∂à</text>
          <text class="popup-title">‰øÆÊîπÊòµÁß∞</text>
          <text class="popup-action-btn save" @click="saveNickname">‰øùÂ≠ò</text>
        </view>
        <view class="popup-body">
          <view class="nickname-generator">
            <text class="nickname-display">{{ tempNickname }}</text>
            <view class="random-button" @click="generateRandomNickname(true)">
              <uni-icons type="refreshempty" size="20" color="#007aff"></uni-icons>
              <text class="random-text">Êç¢‰∏Ä‰∏™</text>
            </view>
          </view>
          <text class="popup-tip">‰∏∫‰Ω†Êé®Ëçê‰∏Ä‰∏™ÊòµÁß∞ÔºåÁÇπÂáª"Êç¢‰∏Ä‰∏™"ÂèØÂàáÊç¢</text>
        </view>
      </view>
    </uni-popup>
    
    <uni-popup ref="bioPopup" type="center" :is-mask-click="false">
      <view class="edit-popup-container">
        <view class="popup-header-with-save">
          <text class="popup-action-btn" @click="closeBioPopup">ÂèñÊ∂à</text>
          <text class="popup-title">‰øÆÊîπÁÆÄ‰ªã</text>
          <text class="popup-action-btn save" @click="saveBio">‰øùÂ≠ò</text>
        </view>
        <view class="popup-body">
          <textarea 
            v-model="tempBio" 
            class="popup-textarea"
            placeholder="ËØ∑ËæìÂÖ•‰∏™‰∫∫ÁÆÄ‰ªã"
            maxlength="200"
            :show-count="true"
          ></textarea>
        </view>
      </view>
    </uni-popup>
    
  </view>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  name: 'EditProfile',
  data() {
    // È¢ÑËÆæÂ§¥ÂÉèÂàóË°®
    const presetAvatarList = Array.from({ length: 25 }, (_, i) => {
      return `https://wait00.oss-cn-shanghai.aliyuncs.com/profile/${i + 1}.png`
    })
    
    // ÊòµÁß∞ËØçÂ∫ì
    const nameStyles = [
      {
        adjectives: ['ËΩØËêå', 'Èò≥ÂÖâ', 'ËΩØÁ≥Ø', 'Ëì¨Êùæ', '‰πñÂ∑ß', 'ÊöñÊöñ', 'Ê≥°Ê≥°', 'Á≥ØÁ≥Ø', 'Ëø∑Ëå´', 'ÂøßÈÉÅ'],
        nouns: ['Ë•øËìùËä±', 'È≤∑È±ºÁÉß', 'ËçâËéìÊ¥æ', 'ÁÉ≠ÂèØÂèØ', 'Â•áÂºÇÊûú', 'Â∞èÈ∫ªËñØ', 'Â∞èÂèØÈ¢Ç', 'Â∞èÊõ≤Â•á', 'ÊòüÂÜ∞‰πê', 'Ëè†ËêùÂåÖ', 'Â∞è‰∫ëÂêû', 'Á≥ØÁ±≥Âõ¢', 'Â∞èÊ≥°Ëäô', 'ÊéíÈ™®Âπ¥Á≥ï']
      },
      {
        adjectives: ['ËôπÊ¢ÖÂçóË∑Ø', '‰∏≠Â±±ÂåóË∑Ø', 'ÂâëÂ∑ùË∑Ø‰∏ä', 'ÈáëÊ≤ôÊ±üË∑Ø', '‰∏úÂ∑ùË∑ØÁöÑ', 'Ëé≤Ëä±ÂçóË∑Ø', 'ËÄÅÂ≠êÊÄùÁª¥', 'ÈáèÂ≠êÊÄùÁª¥', 'Ê±ÇÂÆûÂàõÈÄ†', '‰∏∫‰∫∫Â∏àË°®', '‰∏ΩÂ®ÉÊ≤≥Áïî', 'Ê®±Ê°ÉÊ≤≥Áïî', 'Á¥´Á´πÁöÑ'],
        nouns: ['ÁßãÂÆûÈòÅ', 'Â§èÈõ®ÂéÖ', 'ÂÜ¨ÊúàÂéÖ', 'ÂÜ¨Êó•ÂéÖ', 'Êª°Â§©Êòü', 'ÂÖâÂçé‰π¶Èô¢', 'Â§ßÈõ∂Âè∑Êπæ', 'ÁéØÁêÉÊ∏Ø', 'ËãèÂ∑ûÊ≤≥', 'ÈªÑÊµ¶Ê±ü']
      },
      {
        adjectives: ['ËΩÆÊç¢', 'ÂºÇÊ≠•', 'Ê≠£‰∫§', 'ÂÖ®ÊÅØ', 'ÂÜíÊ≥°', 'ÂºÇÊûÑ', 'Ê≠£Âàô', 'ËµõÂçö', 'Á±ªËÑë', '‰∫íÊòì', 'ËÑâÂÜ≤', 'Áü©Èòµ', 'ÂØºÁîµ'],
        nouns: ['ÈáèÂ≠êÊÄùÁª¥', 'ËÄÅÂ≠êÊÄùÁª¥', 'Â§öÈ°πÂºè', 'ÈÄªËæëÈó®', 'CMOS', 'TTL', '‰∫åÂèâÊ†ë', 'Ë∂ÖÂØº‰Ωì', 'ÂÖâÂ≠êÊùü', 'ÁÆóÂäõÊ†∏', 'Ê®°ÂùóÊú∫', 'Êô∫ËÉΩÁ´Ø', 'ÈÄÜÁü©Èòµ', 'ÂØπÁß∞ÈòµÂ≠ê', 'ÂÖ´Êú®Â§©Á∫ø', 'ÁâõÈ°øÁéØ', 'Â∏åÂ∞î‰ºØÁâπÁ©∫Èó¥', 'pnÁªì', 'Âπ≤Ê∂â‰ª™', 'Á§∫Ê≥¢Âô®']
      },
      {
        adjectives: ['ÊöÆËâ≤', 'ÊòüÊæú', 'È£éÊ†ñ', '‰∫ë‰∏ä', 'ÂæÆÂÖâ', 'ÁßãËâ≤', 'ÊúàÁôΩ', 'ÈÅ•Ëøú', 'Ê∑°Â¢®', 'ÊûùÈó¥', 'Â±±Êµ∑', 'ÈõæËµ∑', 'Â≤ÅÊúà', 'ÈùíËó§', 'ËΩªËàü', 'Ê∏©Êüî', 'Êô¥Á©∫', 'ÊµÅÂÖâ', 'Èõ®ËêΩ', 'Ê∏ÖÊô®'],
        nouns: ['Â∞èÂøÉÂøÉ', 'Â∞èÊ≥°Ê≥°', 'Â∞èÈóÆÂè∑', 'Â∞èÂèπÂè∑', 'Â∞èÊñπÂùó', 'ÊöñÁ≤íÂ≠ê', 'Â∞èÁÅµÊÑü', 'Â∞èËÆ∞ÂøÜ', 'Â∞èËÉΩÈáè', 'Â∞èÈü≥Á¨¶', 'ËΩªÊÉÖÁª™', 'Â∞èÂ∏åÊúõ', 'Â∞èÊ¢¶Â¢É', 'Â∞èËÆ°Âàí', 'Â∞èÊïÖ‰∫ã', 'Â∞èÂøµÂ§¥', 'Â∞èÂÆáÂÆô', 'Â∞èÊúüÂæÖ']
      }
    ]
    
    return {
      // editForm ‰ªçÁî®‰∫éÂ≠òÂÇ®È°µÈù¢ÁöÑ‚ÄúÊúÄÁªà‚ÄùÁä∂ÊÄÅ
      editForm: {
        nickname: '',
        avatarUrl: '',
        user_sex: '',
        major: '', 
        user_introduce: ''
      },
      presetAvatars: presetAvatarList,
      nameStyles: nameStyles,
      currentStyleIndex: 0,
      
      // üî• NEW: Áî®‰∫éÂºπÁ™óÁöÑ‰∏¥Êó∂Êï∞ÊçÆ
      tempNickname: '',
      tempBio: '',
      
      // üî• REMOVED: hasChanges, enableWatch
    }
  },
  
  computed: {
    ...mapState('m_user', ['userBase', 'openid', 'userMajor'])
  },
  
  onLoad() {
    this.initEditForm()
  },
  
  // üî• REMOVED: watch, onBackPress
  
  methods: {
    ...mapMutations('m_user', ['updateUserBase']),
    
    // üî• MODIFIED: ÂàùÂßãÂåñË°®Âçï (ÁßªÈô§ change tracking)
    initEditForm() {
      this.editForm = {
        nickname: this.userBase.nickname || '',
        avatarUrl: this.userBase.avatarUrl || '',
        user_sex: this.userBase.user_sex || '‰øùÂØÜ',
        major: this.userBase.major || '',
        user_introduce: this.userBase.user_introduce || ''
      }
      
      // Â¶ÇÊûúÊòµÁß∞‰∏∫Á©∫ÔºåÂàôËá™Âä®ÁîüÊàê‰∏Ä‰∏™
      if (!this.editForm.nickname) {
        this.generateRandomNickname(false, 'editForm') // Áõ¥Êé•‰øÆÊîπ editForm
      }
      
      // ÂàùÂßãÂåñ‰∏¥Êó∂ÂèòÈáè
      this.tempNickname = this.editForm.nickname
      this.tempBio = this.editForm.user_introduce
    },
    
    // üî• MODIFIED: ÁÆÄÂåñËøîÂõû
    goBack() {
      uni.navigateBack()
    },
    
    // üî• MODIFIED: ÈáçÂëΩÂêç‰∏∫ commitProfileChanges
    // ËøôÊòØ‰∏Ä‰∏™ÈÄöÁî®ÁöÑ‰øùÂ≠òÊñπÊ≥ïÔºåÁî®‰∫é‰øùÂ≠òÊâÄÊúâÂ≠óÊÆµ
    async commitProfileChanges() {
      try {
        uni.showLoading({ title: '‰øùÂ≠ò‰∏≠...', mask: true })
        
        const { data: res } = await uni.$http.post('/users/updateProfile', {
          openid: this.userBase.openid,
          nickname: this.editForm.nickname.trim(),
          avatarUrl: this.editForm.avatarUrl,
          user_sex: this.editForm.user_sex,
          major: this.editForm.major.trim(),
          user_introduce: this.editForm.user_introduce.trim()
        })

        if (res.meta.status === 200) {
          // Êõ¥Êñ∞ Vuex
          this.updateUserBase({
            ...this.userBase,
            ...this.editForm
          })
          
          uni.showToast({ 
            title: '‰øùÂ≠òÊàêÂäü', 
            icon: 'success',
            duration: 1000
          })
        } else {
          throw new Error(res.meta.msg || '‰øùÂ≠òÂ§±Ë¥•')
        }
      } catch (error) {
        console.error('‰øùÂ≠òËµÑÊñôÂ§±Ë¥•:', error)
        uni.showToast({ 
          title: error.message || '‰øùÂ≠òÂ§±Ë¥•', 
          icon: 'none' 
        })
      } finally {
        uni.hideLoading()
      }
    },
    
    // --- 1. Â§¥ÂÉèÈÄªËæë ---
    openAvatarPopup() {
      this.$refs.avatarPopup.open()
    },
    
    closeAvatarPopup() {
      this.$refs.avatarPopup.close()
    },
    
    // üî• MODIFIED: ÈÄâÊã©ÂêéÁ´ãÂàª‰øùÂ≠ò
    async selectAvatar(url) {
      this.editForm.avatarUrl = url
      await this.commitProfileChanges() // Á´ãÂç≥Êèê‰∫§‰øùÂ≠ò
      this.closeAvatarPopup()
    },

    // --- 2. ÊòµÁß∞ÈÄªËæë ---
    openNicknamePopup() {
      // ÊâìÂºÄÂºπÁ™óÊó∂ÔºåÁî® editForm ÁöÑÂÄºË¶ÜÁõñ tempNickname
      this.tempNickname = this.editForm.nickname
      this.$refs.nicknamePopup.open()
    },
    
    closeNicknamePopup() {
      this.$refs.nicknamePopup.close()
    },
    
    async saveNickname() {
      this.editForm.nickname = this.tempNickname // Á°ÆËÆ§‰øÆÊîπ
      await this.commitProfileChanges() // Êèê‰∫§‰øùÂ≠ò
      this.closeNicknamePopup()
    },
    
    // üî• MODIFIED: generateRandomNickname
    // target = 'temp' (Âú®ÂºπÁ™ó‰∏≠) Êàñ 'editForm' (Âú®ÂàùÂßãÂåñÊó∂)
    generateRandomNickname(incrementStyle = false, target = 'temp') {
      let styleIndexToUse = this.currentStyleIndex;
      
      if (incrementStyle) {
        this.currentStyleIndex = (this.currentStyleIndex + 1) % this.nameStyles.length;
        styleIndexToUse = this.currentStyleIndex;
      }
      
      const selectedStyle = this.nameStyles[styleIndexToUse];
      let newName = '';
      let attempts = 0;
      
      const currentName = (target === 'temp') ? this.tempNickname : this.editForm.nickname;
      
      do {
        const adjIndex = Math.floor(Math.random() * selectedStyle.adjectives.length);
        const adj = selectedStyle.adjectives[adjIndex];
        const nounIndex = Math.floor(Math.random() * selectedStyle.nouns.length);
        const noun = selectedStyle.nouns[nounIndex];
        newName = (adj + noun).substring(0, 20);
        attempts++;
      } while (
        newName === currentName &&
        (selectedStyle.adjectives.length > 1 || selectedStyle.nouns.length > 1) &&
        attempts < 10
      );

      if (target === 'temp') {
        this.tempNickname = newName;
      } else {
        this.editForm.nickname = newName;
      }
    },
    
    // --- 3. ÊÄßÂà´ÈÄªËæë ---
    openGenderPopup() {
      uni.showActionSheet({
        itemList: ['Áî∑', 'Â•≥', '‰øùÂØÜ'],
        success: async (res) => {
          const gender = ['Áî∑', 'Â•≥', '‰øùÂØÜ'][res.tapIndex]
          this.editForm.user_sex = gender
          await this.commitProfileChanges() // Á´ãÂç≥Êèê‰∫§‰øùÂ≠ò
        },
        fail: (err) => {
          console.log(err.errMsg)
        }
      })
    },
    
    // --- 4. ÁÆÄ‰ªãÈÄªËæë ---
    openBioPopup() {
      this.tempBio = this.editForm.user_introduce
      this.$refs.bioPopup.open()
    },
    
    closeBioPopup() {
      this.$refs.bioPopup.close()
    },
    
    async saveBio() {
      this.editForm.user_introduce = this.tempBio
      await this.commitProfileChanges()
      this.closeBioPopup()
    }
  }
}
</script>

